<i18n>
{
  "en": {
    "RechargeBalance": "Recharge balance",
    "ChoosePaymentMethod": "Choose payment method",
    "Alipay": "Alipay",
    "Wechat": "Wechat",
    "firstDescribe": "The account balance must be at least 99 yuan to rent the battery. (of which, 99 yuan will be the default deposit. If the account balance is less than 99, the order amount will be deducted from 99. If the account balance is greater than 99, the order amount will be deducted from the amount of 99.)",
    "secondDescribe": "After returning the battery, you can apply to return the balance at any time, and the expected time of arrival is 0-3 working days.",
    "ImmediateRecharge": "Immediate recharge"
  },
  "zh": {
    "RechargeBalance": "充值余额",
    "ChoosePaymentMethod": "请选择支付方式",
    "Alipay": "支付宝",
    "Wechat": "微信",
    "firstDescribe": "账户余额至少有99元才能正常租借充电宝哦。（其中99元将默认作为押金，若账户余额小于99，则订单金额从99中扣除；若账户余额大于99，则订单金额从99外的金额扣除）",
    "secondDescribe": "归还充电宝后,可随时申请退还余额，预计到账时间为0-3个工作日",
    "ImmediateRecharge": "立即充值"
  }
}
</i18n>

<template>
  <div class="recharge">
    <head-top :title='$t("RechargeBalance")' :go-back="true"></head-top>
    <div class="container">
      <div class="column">
        <div class="item" v-for="(item, index) in amount" :key="item" >
          <span :class="{'active': activeIndex === index }" @click="selectAmount(index)">{{item}}</span>
        </div>
      </div>
      <div class="pay">
        <div class="tips">{{ $t('ChoosePaymentMethod') }}</div>
        <ul>
          <li v-for="(item, index) in pay" :key="item.type" @click="selectPay(index)" class="bomPixel">
            <span class="icon"><img :src="item.icon" alt=""></span>
            <span class="text">{{item.text}}</span>
            <span class="circle">
                <img v-show="index === activePayIndex" src="../../assets/wallet_images/click.png" alt="">
                <img v-show="index !== activePayIndex" src="../../assets/wallet_images/quan-default.png" alt="">
              </span>
            </li>
        </ul>
        <p class="ml">
          <img src="../../assets/wallet_images/caution.png" alt="">
          <span>{{ $t('firstDescribe') }}</span>
        </p>
        <p class="dec">
          <span>{{ $t('secondDescribe') }}</span>
        </p>
      </div>
      <div class="sumbit">
        {{ $t('ImmediateRecharge') }}
      </div>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
export default {
  name: 'recharge',
  data () {
    return {
      amount: [100, 50, 20, 10],
      activeIndex: 0,
      activePayIndex: 0,
      pay: [
        {
          icon: require('../../assets/wallet_images/alipay.png'),
          text: this.$t('Alipay'),
          type: 'alipay'
        },
        {
          icon: require('../../assets/wallet_images/wechat.png'),
          text: this.$t('Wechat'),
          type: 'wepay'
        }
      ]
    }
  },

  components: {
    headTop
  },
  methods: {
    selectAmount (index) {
      this.activeIndex = index
    },
    selectPay (index) {
      this.activePayIndex = index
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.recharge{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .column{
      .wh(100%,340px);
      display: flex;
      flex-flow: row wrap;
      align-content: flex-start;
      .item{
        .wh-f(50%, 50%);
        position: relative;
        span{
          display: block;
          .wh-l(230px,120px);
          border: 2px solid #444444;
          border-radius: 5px;
          color: #777777;
          font-weight: 600;
          font-size: 44px;
          .f-center;
          &.active{
            background: #f4da21;
            color: #ffffff;
          }
        }
      }
      .item:nth-child(3) {
        span{
          top: 36%;
        }
      }
      .item:nth-child(4) {
        span{
          top: 36%;
        }
      }
    }
    .pay{
      .tips{
        .wh-l(100%, 80px);
        background:#eeeeee;
        text-align: left;
        padding-left: 30px;
        font-size: 30px;
        color: #aaaaaa;
      }
      ul{
        margin-bottom: 20px;
        li{
          .wh-l(100%, 90px);
          position: relative;
          span{
            display: block;
            img{
              vertical-align: middle;
            }
            &.icon{
              .wh-f(15%,100%);
              img{
                .wh(44px,44px);
              }
            }
            &.text{
              .wh-f(70%,100%);
              text-align: left;
              font-size: 30px;
            }
            &.circle{
              .wh-f(15%,100%);
              img{
                .wh(30px,30px);
              }
            }
          }
        }
      }
      .ml{
        height: auto;
        line-height: 50px;
        font-size: 18px;
        text-align: left;
        position: relative;
        color: #AAAAAA;
        margin-bottom: 10px;
        padding-left: 10%;
        line-height: 36px;
        img{
          height: 22px;
          margin: 0 25px 0 25px;
          position: absolute;
          top: 10px;
          left: 10px;
        }
        span{
          .zoom-font(0.9);
          display: inline-block;
        }
      }
      .dec{
        height: auto;
        line-height: 36px;
        font-size: 18px;
        text-align: left;
        position: relative;
        color: #AAAAAA;
        padding-left: 10%;
        span{
          width: 100%;
          .zoom-font(0.9);
          display: inline-block;
        }
      }
    }
    .sumbit{
      border-radius: 10px;
      font-size: 30px;
      .wh-l(690px,100px);
      background: #f4da21;
      color: #585858;
      position: absolute;
      left: 50%;
      margin-left: -345px;
      bottom: 50px;
      box-shadow: 0px 4px 25px rgba(255, 219, 0, 0.6);
    }
  }
}
</style>



